
.card {
  background-color: #f0f0f0;
  box-shadow: 3px 3px 6px #c0c0c0 ;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 40px 1fr 2fr;
  height: 90%;
  margin: 10px;  
}

.card-header {
  background-color: #e0e0e0;
  padding: 10px;
  text-align: center;
}

.card-body {
  text-align: center;
}

.card-body-component-div {
  padding-top: 10px;
}

.card-footer {
  background-color: #e0e0e0;
  padding: 10px;
}

.card-input {
  padding: 5px;
}

.card-button {
  background-color: #FFF;
  border: 1px solid #c0c0c0;
  /* box-shadow: 3px 3px 3px #c0c0c0;   */
  padding: 10px;
}

.centered {
  text-align: center;
}


.app {
  background-color: #ffffff;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 75px 1fr;
  height: 100vh;
  width: 100vw;
}

.header {
  background-color: #f0f0f0;
  box-shadow: 3px 3px 6px #c0c0c0;
  font-size: 20px;
  margin-left: 30px;
  margin-right: 30px;
  padding: 20px;
  text-align: center;
}

.card-folder {
  align-items: center;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 1fr;  
  padding-left: 20px;
  padding-right: 20px;
}
